
.Landing {
  padding-top: 30px;
  width: 100%;
  background-color: #d6ccc2;
  height: 100%;
}

.flexbox {
  display: flex;
  flex-direction: row;
  height: 400px;
  align-items: center; /* align item vertically middle */
  justify-content: center; /* align iitem horizontally in middle cols */
  height: 100%;
  padding: 20px;
}

.col1 {
  font-size: 30px;
  flex: 0 0 40%;
  color: #FFFFFF;
  padding: 20px;
  width: 100%;
  height: 100%;
  font-weight: bold;
}


.experience {
  padding-top: 20px;
}

.experience-section {
  max-width: 80%;
  margin: 2px auto;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.header-section {
  padding: 10px;
}

.experience-row {
  margin-bottom: 20px;
  border:2px solid #f4f4f4;
}

.col2 {
  flex: 1;
  padding: 20px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
